<template id="alert">
    <div>
        <v-alert
            :value="alert"
            type="error"
        >
            {{errorMsg[0]}}
        </v-alert>
    </div>
</template>

<script>

export default {
    template: '#alert',
    mounted() {

    },
    data: () => ({
        alert: false
    }),
    computed: {
        databaseLocalHost() {
            return this.$store.state.databaseLocalHost;
        },
        localHostName() {
            return this.$store.state.localHostName;
        },
        errorMsg() {
            return this.$store.state.errorMsg;
        },
        showError() {
            return this.$store.state.showError;
        },
    },
    watch: {
        showError(val) {
            if(val) {
                this.alert = true;
            } 
        }
    },
    methods: {
        hideAlert() {
            console.log("Hello");   
            this.$store.dispatch('changeShowError', false);
            this.alert = false;
        },
        alertExists() {
            return this.errorMsg.length > 0 ? true : false; 
        },
    }
}
</script>

<style scoped>
    .v-alert {
        position: fixed;
        left: 50%;
        bottom: 50px;
        transform: translate(-50%, -50%);
        margin: 0 auto; 
        z-index: 9000 !important;
    }
</style>
